@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  background: #ebebeb;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: 'Share Tech Mono';
}

.App {
  height: 400px;
  width: 300px;
  background: #ebebeb;
  position: relative;
}

.Display {
  background: #000000;
  color: #ffffff;
  text-shadow: 0 0 5px rgba(89, 195, 195, 0.5);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 29px;
  box-sizing: border-box;
  height: 20%;
  overflow: hidden;
  font-size: 24px;
  position: relative;
}

.Button {
  background: #ffffff;
  display: -webkit-flex;
  display: flex;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.05);
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 25%;
  font-size: 24px;
  height: 20%;
  min-width: 25%;
}

.Button[data-size='2'] {
  height: 40%;
}

.Button[data-value='null'] {
  pointer-events: none;
}

.Button:hover {
  cursor: pointer;
}

.Buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 80%;
}

